import React, { useState, useEffect } from 'react';
import { getAllSubscription } from '../../../../../store/actions/SubscriptionActions';
import PerfectScrollbar from 'react-perfect-scrollbar';
import CloseIcon from '@material-ui/icons/Close';
import { useDispatch, useSelector } from 'react-redux';
import { setMessage } from '../../../../../store/actions/MessageActions';
import useStyles from '../../../../DialogStyles';
import { getAllPlanDropdown, getAllPlanOptionsDropdown, getAllPlanDropdownOption } from '../../../../../store/actions/SubscriptionActions';
import { userProfileSearch } from '../../../../../store/actions/AddUserActions';

import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    Typography,
    Grid,
    Link,
    Divider,
    TextField,
    Autocomplete,
    Paper,
    Select,
    CircularProgress
} from '@material-ui/core';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { RHFSelect, FormProvider, RHFTextField } from '../../../../../components/hook-form';
import { addPlanSubscription } from '../../../../../store/actions/FAQActions';
import { calculatePlan, checkDiscountCode, checkVoucherCode } from '../../../../../services/PlanSubscription';
import { getSubscriptions } from '../../../../../services/subscriptions';
import { FormControl } from '@mui/material';
import { useForm } from 'react-hook-form';

const STATUS_ITEMS = [
    { id: 0, status: 'ONLINE' },
    { id: 1, status: 'CASH' },
    { id: 2, status: 'NOT_APPLICABLE' },
    { id: 3, status: 'NEFT' },
    { id: 4, status: 'GPAY_WALLET' },
    { id: 5, status: 'RAZORPAY' },
    { id: 6, status: 'FREE' }
];

const validationSchema = yup.object({
    planId: yup.string().required('Select a Plan'),
    planOptionId: yup.string().required('Select a Plan Option'),
    paymentMode: yup.string().required('Select a Payment mode'),
    transactionId: yup.string().required('Transaction Id Is Required')
});

const initialValues = {};

export default function AddSubscription(props) {
    const { isOpen, setOpen, isEditData } = props;
    const [userId, setUserId] = useState(null);
    const [profileId, setProfileId] = useState(null);
    const classes = useStyles();
    const dispatch = useDispatch();
    const [selectedOptions, setSelectedOptions] = useState(null);
    const [planId, setPlanId] = useState('');
    const [planOptionId, setplanOptionId] = useState('');
    const [price, setPrice] = useState('');
    const [total, setTotal] = useState('');
    const [storedprice, setStoredprice] = useState('');
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    const { getSearchedProfileData } = useSelector((state) => state?.addUser);
    const { getAllPlans } = useSelector((state) => state?.subscriptions);
    const { getUserPlan } = useSelector((state) => state?.subscriptions);
    const { allPlanOptions } = useSelector((state) => state?.subscriptions);
    const [errorResp, setErrorResp] = useState(null);
    const newData = getAllPlans?.content;

    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(validationSchema),
        defaultValues: initialValues
    });

    const { handleSubmit, setValue, watch, errors, control } = methods;

    const plan_Id = watch('planId');
    const plan_OptionId = watch('planOptionId');
    const gift_VoucherCode = watch('giftVoucherCode');
    const discount_Code = watch('discountCode');
    const payment_Mode = watch('paymentMode');
    const transaction_Id = watch('transactionId');

    const onSubmit = async (data) => {
        try {
            setLoading(true);
            setError(null);
            if (discount_Code !== '' && gift_VoucherCode !== '') {
                const payload1 = {
                    userId: userId,
                    profileId: profileId,
                    planId: plan_Id,
                    planOptionId: plan_OptionId,
                    discountCode: discount_Code,
                    giftVoucherCode: gift_VoucherCode,
                    paymentMode: payment_Mode,
                    pricePaid: price,
                    orderId: transaction_Id
                };
                await dispatch(
                    addPlanSubscription(payload1, () => {
                        dispatch(getAllSubscription());
                    })
                );
            } else {
                const payload2 = {
                    userId: userId,
                    profileId: profileId,
                    planId: plan_Id,
                    planOptionId: plan_OptionId,
                    paymentMode: payment_Mode,
                    pricePaid: price,
                    orderId: transaction_Id
                };

                try {
                    const res = await dispatch(
                        addPlanSubscription(payload2, () => {
                            setTimeout(() => {
                                window.location.reload();
                                dispatch(getAllSubscription());
                            }, 3000);
                        })
                    );
                } catch (error) {
                    setTimeout(() => {
                        window.location.reload();
                        dispatch(getAllSubscription());
                    }, 3000);
                    setLoading(false);
                    // Handle the error, e.g., display an error message to the user
                    console.error('Error adding plan subscription:', error);
                }

            }
            handlePopupClose();
        } catch (error) {
            setError(error.message);
        }
    };

    const handlePopupClose = () => {
        setOpen(false);
    };

    const handleChange = (event, value) => {
        setSelectedOptions(value);
        setUserId(value?.userId);
        setProfileId(value?.profileId);
        setErrorResp(null);
    };

    const handleInputChange = (event, value) => {

        dispatch(userProfileSearch(value, 'STUDENT'));
    };

    const handleGetUsers = () => {
        if (!userId || !profileId) {
            return;
        }
        dispatch(getAllPlanDropdownOption(userId, profileId));
    };

    const CouponcodeCheckAndApply = async () => {
        const DiscountCodepayload = {
            planId: plan_Id,
            planOptionId: plan_OptionId,
            userId: userId,
            profileId: profileId,
            discountCode: discount_Code,
            giftVoucherCode: gift_VoucherCode
        };


        await checkDiscountCode(DiscountCodepayload, dispatch, setMessage).then((res) => {
            calculatePlan(DiscountCodepayload, dispatch, setMessage)
                .then((res) => {
                    setTotal(res);
                    setStoredprice(res?.priceToBePaid);

                    if (!storedprice) {
                        setPrice(res?.priceToBePaid);
                    } else {
                        let a = price - res?.priceToBePaid;
                        setPrice(a);
                    }
                })
                .catch((error) => {
                    console.log(error);
                });
        });
    };

    const handleDiscountCode = (e) => {
        CouponcodeCheckAndApply(dispatch, setMessage);
    };

    const VouchercodeCheckAndApply = async () => {
        const Voucherpayload = {
            planId: plan_Id,
            planOptionId: plan_OptionId,
            userId: userId,
            profileId: profileId,
            giftVoucherCode: gift_VoucherCode,
            discountCode: discount_Code
        };
        gift_VoucherCode &&
            (await checkVoucherCode(Voucherpayload, dispatch, setMessage).then((res) => {
                calculatePlan(Voucherpayload, dispatch, setMessage)
                    .then((res) => {
                        setTotal(res);
                        setStoredprice(res?.priceToBePaid);
                        if (!storedprice) {
                            setPrice(res?.priceToBePaid);
                        } else {
                            let b = price - res?.priceToBePaid;
                            setPrice(b);
                        }
                    })
                    .catch((error) => {
                        console.log(error);
                    });
            }));
    };

    const handleVoucherCode = (e) => {
        VouchercodeCheckAndApply(dispatch, setMessage);
    };

    const handlePlanChange = (event, value) => {
        const val1 = event.target.value;
        setPlanId(val1);
        dispatch(getAllPlanOptionsDropdown(event.target.value));
    };

    useEffect(() => {
        dispatch(getAllPlanOptionsDropdown(plan_Id));
    }, [plan_Id]);

    const handlePlanOptionChange = (event, value) => {
        const val = event.target.value;
        setplanOptionId(val);
        const payload = {
            planId: planId,
            planOptionId: val,
            userId: userId,
            profileId: profileId
        };

        if (val) {
            calculatePlan(payload, dispatch, setMessage)
                .then((res) => {
                    setTotal(res);
                    setPrice(res?.priceToBePaid);
                })
                .catch((error) => {
                    console.log(error);
                });
        }
    };
    useEffect(() => {
        const payload = {
            planId: plan_Id,
            planOptionId: plan_OptionId,
            userId: userId,
            profileId: profileId
        };
        plan_OptionId &&
            calculatePlan(payload, dispatch, setMessage)
                .then((res) => {
                    setTotal(res);
                    setPrice(res?.priceToBePaid);
                })
                .catch((error) => {
                    console.log(error);
                    setErrorResp(error);
                });
    }, [plan_OptionId]);

    useEffect(() => {
        dispatch(userProfileSearch('', 'STUDENT'));
        dispatch(getAllPlanDropdown());
    }, []);

    return (
        <div>
            <Dialog open={isOpen}>
                <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                    <DialogTitle className={classes.dialogTitle}>
                        <div className={classes.dialogTitleChild}>
                            <Typography style={{ fontSize: '20px' }}> Adding Subscription </Typography>
                            <Link to="#" onClick={handlePopupClose}>
                                <CloseIcon className={classes.closeIcon} />
                            </Link>
                        </div>
                    </DialogTitle>
                    <PerfectScrollbar className={classes.ScrollHeight}>
                        <Grid container direction="column" spacing={1}>
                            <DialogContent>
                                <Grid item>
                                    <label>
                                        Users <span className={classes.mandatory}>*</span>
                                    </label>
                                    <Autocomplete
                                        autoHighlight
                                        id="students"
                                        options={getSearchedProfileData}
                                        getOptionLabel={(option) => option?.firstNameLastName + " - " + option?.profileId}
                                        onChange={handleChange}
                                        onInputChange={handleInputChange}
                                        filterSelectedOptions
                                        renderInput={(params) => <TextField {...params} variant="outlined" />}
                                    />
                                </Grid>
                                <Grid item>
                                    <Button
                                        variant="contained"
                                        size="small"
                                        onClick={handleGetUsers}
                                        marginTop="10px"
                                        style={{ marginTop: '10px' }}
                                    >
                                        Get Plans
                                    </Button>
                                </Grid>
                                <Grid item>
                                    <label>
                                        Plan <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFSelect name="planId" variant="outlined" size="small">
                                        <option value="">Select</option>
                                        {getUserPlan?.map((item, index) => (
                                            <option
                                                key={item?.id}
                                                value={item?.id}
                                                defaultValue={item?.id === newData?.filter((lang) => lang.id === isEditData?.planId)[0]?.id}
                                            >
                                                {item?.name}
                                            </option>
                                        ))}
                                    </RHFSelect>
                                </Grid>
                                <Grid item>
                                    <label>
                                        Plan option <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFSelect name="planOptionId" variant="outlined" size="small">
                                        <option value="">Select</option>
                                        {allPlanOptions?.map((item, index) => (
                                            <option
                                                key={item?.id}
                                                value={item?.id}
                                                defaultValue={
                                                    item?.id ===
                                                    allPlanOptions?.filter((lang) => lang.id === isEditData?.planOptionId)[0]?.id
                                                }
                                            >
                                                {item?.type}
                                            </option>
                                        ))}
                                    </RHFSelect>
                                </Grid>
                                <Grid item>
                                    <Typography style={{ fontWeight: 'lighter', fontSize: '12px', marginTop: '16px', color: '#3fb2cf' }}>
                                        Voucher Code
                                    </Typography>
                                    <Grid container justifyContent="space-between" alignItems="baseline">
                                        <Grid item>
                                            <RHFTextField variant="outlined" name="giftVoucherCode" size="small" autoComplete="off" />
                                        </Grid>
                                        <Grid item>
                                            <Button
                                                variant="contained"
                                                size="small"
                                                onClick={handleVoucherCode}
                                                marginTop="10px"
                                                style={{ marginTop: '10px' }}
                                            >
                                                Apply
                                            </Button>
                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Grid item>
                                    <Typography style={{ fontWeight: 'lighter', fontSize: '12px', marginTop: '16px', color: '#3fb2cf' }}>
                                        Discount Coupon
                                    </Typography>
                                    <Grid container justifyContent="space-between" alignItems="baseline">
                                        <Grid item>
                                            <RHFTextField variant="outlined" name="discountCode" size="small" autoComplete="off" />
                                        </Grid>
                                        <Grid item>
                                            <Button
                                                variant="contained"
                                                onClick={handleDiscountCode}
                                                size="small"
                                                style={{ marginTop: '10px' }}
                                            >
                                                Apply
                                            </Button>
                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Grid item>
                                    <label className={classes.label}>
                                        Payment Mode <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFSelect name="paymentMode" variant="outlined" size="small">
                                        <option value="">Select</option>
                                        {STATUS_ITEMS?.map((item, index) => (
                                            <option key={index} value={item?.status}>
                                                {item?.status}
                                            </option>
                                        ))}
                                    </RHFSelect>
                                </Grid>
                                <Grid item>
                                    <label className={classes.label}>
                                        Price <span className={classes.mandatory}>*</span>
                                    </label>
                                    <TextField
                                        disabled
                                        value={total?.priceToBePaid ?? ''}
                                        size="small"
                                        fullWidth
                                        name="priceToBePaid"
                                        required
                                    />
                                </Grid>
                                <Grid item>
                                    <label className={classes.label}>
                                        Transaction ID <span className={classes.mandatory}>*</span>
                                    </label>
                                    <RHFTextField variant="outlined" name="transactionId" size="small" autoComplete="off" />
                                </Grid>
                            </DialogContent>
                        </Grid>
                    </PerfectScrollbar>
                    <Divider style={{ marginTop: 15 }} />
                    <DialogActions className={classes.dialogAction}>
                        <Button variant="outlined" color="secondary" className={classes.buttonCancel} onClick={handlePopupClose}>
                            Close
                        </Button>
                        <Button variant="contained" disabled={loading || errorResp !== null} type="submit" className={classes.buttonSubmit}>
                            {loading ? <CircularProgress size={24} style={{ color: 'white' }} /> : 'Add'}
                        </Button>
                        {error && <p>{error}</p>}
                    </DialogActions>
                </FormProvider>
            </Dialog>
        </div>
    );
}
