import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getAllSubscription } from '../../../../store/actions/SubscriptionActions';
import MainCard from '../../../../ui-component/cards/MainCard';
import { withStyles, useTheme } from '@material-ui/styles';
import {
    Grid,
    Button,
    IconButton,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Typography,
    Tooltip,
    CircularProgress,
    Box,
    Checkbox
} from '@material-ui/core';
import { IconSearch } from '@tabler/icons';
import SubjectIcon from '@material-ui/icons/Subject';
import AddSubscription from './add-subscription';
import AddIcon from '@material-ui/icons/Add';
import VisibilityIcon from '@material-ui/icons/Visibility';
import { useHistory } from 'react-router-dom';
import useStyles from '../../../ViewTableStyles';
import useUserRole from '../../../../hooks/useUserRole';
import { getSubscriptionLibrary } from '../../../../services/export';
import { Alert, Snackbar, useMediaQuery } from '@mui/material';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function AllSubscription() {
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'SCHOOL_ADMIN','SUPER_ADMIN'] });
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const dispatch = useDispatch();
    const theme = useTheme();
    const history = useHistory();
    const [value, setValue] = useState('');
    const [openAddDeliveryPlans, setOpenAddDeliveryPlans] = useState(false);
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);
    const [isPlanUpdateData, setPlanUpdateData] = useState(null);
    const [selected, setSelected] = useState([]);
    const [loading, setLoading] = useState(false);
    const [dowmloadFail, setDownloadFail] = useState(false);

    const isSelected = (name) => selected.indexOf(name) !== -1;

    const { allSubscription } = useSelector((state) => state?.subscriptions);
    const newData = allSubscription?.content;

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        // setPage(0);
    };

    const handleViewPlans = (id) => {
        history.push(`subscription/${id}`);
    };
    const handleDeliveryPlan = (id) => {
        history.push(`orders/delivery-plan/${id}`);
    };

    const handlePlans = (data) => {
        setOpenAddDeliveryPlans(true);
        setPlanUpdateData(data);
    };
    const exportSubscription = async () => {
        setDownloadFail(false);
        setLoading(true);

        await getSubscriptionLibrary(selected)
            .then((r) => {
                setLoading(false);
            })
            .catch((error) => {
                setLoading(false);
                setDownloadFail(true);
                console.log(error);
            });
    };

    const handleSelectAllClick = (event) => {
        if (event.target.checked) {
            const newSelecteds = newData.map((n) => n.id);
            setSelected(newSelecteds);
            return;
        }
        setSelected([]);
    };

    const handleClick = (event, name) => {
        const selectedIndex = selected.indexOf(name);

        let newSelected = [];

        if (selectedIndex === -1) {
            newSelected = newSelected.concat(selected, name);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(selected.slice(1));
        } else if (selectedIndex === selected.length - 1) {
            newSelected = newSelected.concat(selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
        }

        setSelected(newSelected);
    };

    useEffect(() => {
        dispatch(getAllSubscription(value ? value : '', page + 1, rowsPerPage));
    }, [rowsPerPage, page, value]);

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <SubjectIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; Library Subscriptions </span>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid container spacing={3} style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <Grid item xs={12} sm={6} md={6}>
                            <div>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-header"
                                    value={value}
                                    onChange={(e) => {
                                        setValue(e?.target?.value);
                                        setPage(0);
                                    }}
                                    placeholder="Search"
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                />
                            </div>
                        </Grid>
                        <Grid item display="flex" justifyContent="center">
                            <Button
                                variant="contained"
                                color="primary"
                                style={{ boxShadow: '0 0.5em 0.5em 0.1em #68c2d9', marginBottom: '50px' }}
                                onClick={exportSubscription}
                            >
                                Export
                            </Button>
                        </Grid>
                        <Grid item>
                            <div className={classes.btn}>
                                {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        onClick={() => {
                                            setOpenAddDeliveryPlans(true);
                                            setPlanUpdateData(null);
                                        }}
                                        className={classes.btnAddDeliveryPlan}
                                    >
                                        <AddIcon /> Subscription
                                    </Button>
                                )}
                            </div>
                        </Grid>
                    </Grid>
                    <Grid sm={2} xs={12} item display="flex" justifyContent="center">
                        {loading ? (
                            <>
                                <h7>Downloading...</h7>{' '}
                                <Box sx={{ display: 'flex' }}>
                                    <CircularProgress />
                                </Box>
                            </>
                        ) : (
                            <></>
                        )}
                        {dowmloadFail ? <Alert severity="error">Downloading Failed !</Alert> : <> </>}
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell padding="checkbox">
                                        <Checkbox
                                            indeterminate={selected?.length > 0 && selected?.length < newData?.length}
                                            checked={newData?.length > 0 && selected?.length === newData?.length}
                                            onChange={handleSelectAllClick}
                                            inputProps={{ 'aria-label': 'select all items' }}
                                        />
                                    </TableCell>
                                    <TableCell> FullName(CUS Id) </TableCell>
                                    <TableCell> Plan Title </TableCell>
                                    <TableCell align="center"> Start Date </TableCell>
                                    <TableCell align="center"> End Date </TableCell>
                                    <TableCell align="center"> Status </TableCell>
                                    <TableCell align="right"> Actions </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!newData ? (
                                <caption style={{ textAlign: 'center' }}>Sorry! No data available</caption>
                            ) : (
                                <TableBody>
                                    {newData?.map((row, index) => {
                                        const isItemSelected = isSelected(row?.id);

                                        const labelId = `enhanced-table-checkbox-${index}`;
                                        return (
                                            <StyledTableRow key={row?.id}>
                                                <TableCell padding="checkbox">
                                                    <Checkbox
                                                        checked={isItemSelected}
                                                        inputProps={{ 'aria-labelledby': labelId }}
                                                        onChange={(event) => handleClick(event, row?.id)}
                                                    />
                                                </TableCell>
                                                <TableCell>
                                                    {row?.userLimitedInfo?.fullName}({row?.userLimitedInfo?.profileId})
                                                </TableCell>
                                                <TableCell>
                                                    {row?.planLimitedInfo?.booksCount}B X {row?.planOptionLimitedInfo?.durationInMonths}M
                                                </TableCell>
                                                <TableCell align="center">{row?.startDate}</TableCell>
                                                <TableCell align="center">{row?.endDate}</TableCell>
                                                <TableCell align="center">
                                                    <Typography
                                                        className={classes.status}
                                                        style={{
                                                            backgroundColor:
                                                                new Date(row?.endDate) < new Date()
                                                                    ? 'INACTIVE'
                                                                    : 'ACTIVE'
                                                                    ? theme.palette.success.main
                                                                    : theme.palette.warning.dark
                                                        }}
                                                    >
                                                        {new Date(row?.endDate) < new Date() ? 'INACTIVE' : 'ACTIVE'}
                                                    </Typography>
                                                </TableCell>

                                                <TableCell align="right">
                                                    <Tooltip title="View Subscription">
                                                        <IconButton onClick={() => handleViewPlans(row.id)}>
                                                            <VisibilityIcon color="primary" style={{ cursor: 'pointer' }} />
                                                        </IconButton>
                                                    </Tooltip>

                                                    <Tooltip title="View Delivery Plan">
                                                        <IconButton onClick={() => handleDeliveryPlan(row.id)}>
                                                            <VisibilityIcon color="primary" style={{ cursor: 'pointer' }} />
                                                        </IconButton>
                                                    </Tooltip>
                                                </TableCell>
                                            </StyledTableRow>
                                        );
                                    })}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[25, 50, 100]}
                        component="div"
                        count={allSubscription?.totalElements || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>
                {openAddDeliveryPlans && (
                    <AddSubscription
                        isOpen={openAddDeliveryPlans}
                        setOpen={setOpenAddDeliveryPlans}
                        isEditData={isPlanUpdateData}
                        getAllSubscription={getAllSubscription}
                    />
                )}
            </MainCard>
        </>
    );
}
